.content {
  margin: auto;
  margin-top: 10px;
  width: 110%;
  height: auto;
  background-color:#005ce6;
  padding: 20px;
  border-radius: 8px;
}

.title {
  padding-top: 80px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 0.15rem;
  color:#95ec09;
}

label {
  color: rgb(233, 225, 225);
}

.result-container {
  text-align: center;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 0.15rem;
  padding: 20px 0 0;
}

